body {
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 600;
}

.employee {
  display: flex;
  width: 300px;
  flex-direction: row;
  align-items: center;
  &__name {
    margin-left: 10px;
  }
  &__img {
    width: 100%;
    border-radius: 50%;
    &--container {
      border: solid 3px rgb(191, 191, 191);
      border-radius: 48%;
      padding: 5px;
      height: 100px;
      width: 100px;
      display: inline-block;
    }
  }
  &__shadow {
    box-shadow: 5px 10px #888888;
  }
  &__parents--container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  &__parent {
    &--branch {
      width: 3px;
      height: 30px;
      background-color: grey;
      margin-top: -2px;
      margin-bottom: -2px;
    }
    &--center {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      width: 300px;
      margin-left: 180px;
    }
  }
  &__children {
    display: flex;
    flex-direction: row;
    align-items: center;
  }
}

